<template>
  <div class="viewWidth">
    <div class="center">
      <div class="title">
        <h1>Git</h1>
        <p>版本管理</p>
        <a href=""></a>
      </div>
      <!-- li>h3+(ul>li>pre.prettyprint) -->
      <ul>
        <li class="a">
          <h3>Git 基本指令有哪些?</h3>
          <table>
            <tr>
              <th>指令</th>
              <th>說明</th>
            </tr>
            <tr>
              <td>$ git init</td>
              <td>使用 git 對當前目錄進行版本控制</td>
            </tr>
            <tr>
              <td>$ git add --all</td>
              <td>一口氣把全部的檔案加入到暫存區</td>
            </tr>
            <tr>
              <td>$ git status</td>
              <td>是用來查詢現在目錄的狀態 (status 記錄此目錄歷史的指令與狀態)</td>
            </tr>
            <tr>
              <td>$ git commit -m "初始化 init"</td>
              <td>將暫存區的內容永久的存活下來，"初始化 init"為此次commit 的名稱</td>
            </tr>
            <tr>
              <td>$ git commit --allow-empty -m "增加一個空的 commit"</td>
              <td>做一個空的 commit</td>
            </tr>
            <tr>
              <td>$ git commit -a -m "update content"</td>
              <td>直接省略 add 動作直接到 commit，但對新加入的檔案無效</td>
            </tr>
            <tr>
              <td>$ git log </td>
              <td>檢視所有 commit 的詳細資料</td>
            </tr>
            <tr>
              <td>$ git log --oneline</td>
              <td>檢視精簡的 commit 的資料</td>
            </tr>
            <tr>
              <td>$ git commit --amend</td>
              <td>進入 Vim 模式修改</td>
            </tr>
            <tr>
              <td>$ git commit --amend -m "更改名稱"</td>
              <td>如此可以修改最後一次 commit 的訊息</td>
            </tr>
            <tr>
              <td>$ git reflog</td>
              <td>找尋被刪除的 commit (可以在30天內找回誤刪除的 commit )</td>
            </tr>
            <tr>
              <td>$ git log -p dog.txt</td>
              <td>檢視 dog.txt 的commit 紀錄</td>
            </tr>
            <tr>
              <td>$ git blame index.html </td>
              <td>這個檔案的程式碼每行的修改紀錄
              </td>
            </tr>
            <tr>
              <td>$ git reset e12d8ef^</td>
              <td>拆解e12d8ef這個的 commit</td>
            </tr>
            <tr>
              <td>$ git branch</td>
              <td>檢視當前的分支</td>
            </tr>
            <tr>
              <td>$ git branch cat</td>
              <td>新增一個名稱為 cat 的分支</td>
            </tr>
            <tr>
              <td>$ git branch -m cat tiger</td>
              <td>修改分支的名稱(git branch -m舊名稱 新名稱)</td>
            </tr>
            <tr>
              <td>$ git branch -d dog</td>
              <td>刪除 dog這個分支</td>
            </tr>
            <tr>
              <td>$ git branch -D dog</td>
              <td>強制刪除尚未合併的 dog 分支</td>
            </tr>
            <tr>
              <td>$ git checkout cat</td>
              <td>切換到 cat分支</td>
            </tr>
            <tr>
              <td>$ git checkout b1ea289</td>
              <td>將當前的 HEAD 切換至b1ea2895 這個 commit 上</td>
            </tr>
            <tr>
              <td>$ git merge cat</td>
              <td>合併 cat 這個分支</td>
            </tr>
          </table>
        </li>
        <li>
          <h3>Git 操作技巧有哪些?</h3>
          <ul>
            <li>
              <h4>如何 Push 上傳 GitHub?</h4>
              <a
                href="https://gitbook.tw/chapters/github/push-to-github.html"
                target="_blank"
              >如何 Push 上傳到 GitHub？</a>
              <pre class="prettyprint">
//0.準備前面的 git init、git add、git commit…等等
//1.準備把東西推上遠端的Git 伺服器。首先需要設定一個端節的節點
$ git remote add origin git@github.com:kaochenlong/practice-git.git
$ git push -u origin master //2.推上東西
//2-1.如果你能理解上面這個指令的意思，你就可以再做一些變化。例如你的遠端節點叫做 dragonball，而且你想把 cat 分支推上去：
$ git push dragonball cat</pre>
            </li>
            <li>
              <h4>想要刪除某幾個 Commit 或是調整 Commit 的順序?</h4>
              <a
                href="https://gitbook.tw/chapters/rewrite-history/remove-and-reorder-commit.html"
                target="_blank"
              >想要刪除某幾個 Commit 或是調整 Commit 的順序</a>
              <p>移動 Commit...</p>
            </li>
            <li>
              <h4>
                把多個 Commit 合併成一個 Commit?
              </h4>
              <a
                href="https://gitbook.tw/chapters/rewrite-history/merge-multiple-commits-to-one-commit.html"
                target="_blank"
              >把多個 Commit 合併成一個 Commit</a>
              <pre class="prettyprint">
$ git log --oneline
27f6ed6 (HEAD -> master) add dog 2
2bab3e7 add dog 1
ca40fc9 add 2 cats
1de2076 add cat 2
cd82f29 add cat 1
382a2a5 add database settings
bb0c9c2 init commit                

$git rebase -i bb0c9c2 
//在互動模式的紀錄由上而下是從最舊到最新，跟 git log 指令所呈現的結果是相反的。
//要開始處理的 commit，接著一樣再次出現 Vim 編輯器視窗
pick 382a2a5 add database settings
pick cd82f29 add cat 1
pick 1de2076 add cat 2
pick ca40fc9 add 2 cats
pick 2bab3e7 add dog 1
pick 27f6ed6 add dog 2

# Rebase bb0c9c2..27f6ed6 onto bb0c9c2 (6 commands)
#
# Commands:
# ...[略]...
//使用 squash 指令，將上面內容修改如下
pick 382a2a5 add database settings
pick cd82f29 add cat 1
squash 1de2076 add cat 2
squash ca40fc9 add 2 cats
pick 2bab3e7 add dog 1
squash 27f6ed6 add dog 2
//存檔並離開，會開始進行 Rabase</pre>
              <p>上面所發生的事情是<br>
                1.最後一行的 27f6ed6 會跟前一個 Commit 2bab3e7 進行合併，也就是 add dog 1 跟 add dog 2 這個 Commit 會合在一起。<br>
                倒數第三號的 ca40fc9 會跟前一個 Commit 1de2076 合併，但因為 1de2076 又會再往前一個 Commit cd82f29 合併，所以整個跟 cat 有關的這三個 Commit 會併成同一個。</p>
            </li>
            <li>
              <h4>我可以從過去的某個 Commit 再長一個新的分支出來嗎？</h4>
              <a
                href="https://gitbook.tw/chapters/branch/branch-from-old-commit.html"
                target="_blank"
              >我可以從過去的某個 Commit 再長一個新的分支出來嗎？</a>
              <pre class="prettyprint">$ git checkout 657fce7 //移動到那個分支
$ git checkout -b brid //你可以從現在這個 Commit 開新的分支出去，讓我們直接使用 Checkout 指令的 -b 參數直接建立分支並自動切換過去</pre>
            </li>
            <li>
              <h4>合併發生衝突了，怎麼辦？</h4>
              <a
                href="https://gitbook.tw/chapters/branch/fix-conflict.html"
                target="_blank"
              >合併發生衝突了，怎麼辦？</a>
              <p>去看合併有問題的檔案，並直接看要使用哪一分支的內容，最後add 與 commit 完成合併，完成合併</p>
            </li>
            <li>
              <h4>查詢被刪掉的分支?</h4>
              <p>你可以從 git reflog 指令去翻翻看，Reflog 預設會保留 30 天，所以 30 天內應該都還找得到。</p>
              <pre class="prettyprint">$ git reflog</pre>
            </li>
            <li>
              <h4>合併兩個分支?</h4>
              <p>我從 master 分支開了一個 cat 分支，並且做了兩次 Commit。
                任務執行的差不多了，就要準備合併回來了。如果我想要 master 分支來合併 cat 分支的話，我會先切回 master 分支
              </p>
              <pre class="prettyprint">$ git checkout master //切換分支
$ git merge cat //合併 cat 分支</pre>
              <p>在 cat 分支新增的 cat1.html 跟 cat2.html，因為 master 現在已經合併 cat 分支，所以現在在 master 分支也有一份了。</p>
            </li>
            <li>
              <h4>切換分支的檔案?</h4>
              <p>目前 master 分支有4個檔案，切換到 cat 分支後新增兩個檔案，再切換到 master 分支。<br>
                咦？剛剛那兩個檔案不見了！別擔心，其實他們都還在的，只是在不同的分支而已，只要切回(checkout) cat 分支，檔案就會出現了。
              </p>
            </li>
            <li>
              <h4>HEAD 是什麼東西？</h4>
              <p>HEAD 是一個指標，指向某一個分支，通常你可以把 HEAD 當做「目前所在分支」看待。</p>
            </li>
            <li>
              <h4>在 Vim 模式中操作修改最後一個 commit 的名稱 ?</h4>
              <pre class="prettyprint">
$ git commit –amend // 開始修改最後一個 commit
e  //輸入e，進入編輯
a //輸入a，進入 append，開始修改名稱
esc //按下後退到 Normal 模式
:wq //進行儲存並關閉這個檔案</pre>
            </li>
            <li>
              <h4>為什麼要二段式這麼麻煩？</h4>
              <p>先 add 再 commit 這樣二段式感覺可能有點麻煩，但也是有好處的。<mark>你可以想像你有一個倉庫，在倉庫門口有個小廣場，這個廣場的概念就像跟暫存區一樣，你把要存放到倉庫的貨物先放到這邊（git add），然後等收集的差不多了就可以打開倉庫門，把在廣場上的貨物送進倉庫裡（git commit），並且記錄下來這批貨是什麼用途的、誰送來的。</mark><br>當然，你也可以每來一件貨物就開倉門存一次、紀錄一次，但這樣一來開倉庫的次數就會非常多。老實說這樣也沒什麼過錯，只是因為太過零碎的 Commit，有時候要查閱紀錄可能會有點不太方便；另外，過於零碎的 Commit 也可能給你的同事帶來一些困擾，例如要進行 Code Review 的時候，比較有整理的 Commit 可以一次看到比較完整的內容，而不需要一個一個 Commit 慢慢看。
              </p>
            </li>
            <li>
              <h4>所以，什麼時候要 Commit？</h4>
              <p>
                這個問題沒有標準答案，你可以很多檔案修改好再一口氣全部一起 Commit，也可只改一個字就 Commit。常見的 Commit 的時間點有：<br>
                1.修改幾個字<br>
                2.完成一個頁面<br>
                3.完成一個進度<br>
                4.完成一個小系統<br>
                5.下班前來一個 Commit<br>
                x.你想要 Commit 的時候就可以 Commit。</p>
            </li>
            <li>
              <h4>平行時空，關於修改 Commit 名稱的時機?</h4>
              <p>
                記得在 Git 中 Push 出去之後，就不要再更改 Commit 的訊息，否則可能會造成其它人的困擾。</p>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "git"
  };
</script>

<style lang="scss" scoped>
  @import "../../../style/common.scss";
  @import "../../../style/pretty.scss";
  @import "../../../style/style.scss";

  .a {
    @include backgroundStyle("", flex, center, wrap);
    h3 {
      width: 100%;
    }
  }
  table {
    border-spacing: 0;
  }
  td {
    padding: 0.2rem;
  }
  td:first-child,
  th:first-child {
    border-radius: 5px 0 0 5px;
  }
  td:last-child,
  th:last-child {
    border-radius: 0 5px 5px 0;
  }
  tr:nth-of-type(2n + 1) {
    background-color: $color3;
  }
</style>
